@import url(https://fonts.googleapis.com/css?family=Courgette|Space+Mono);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --space-red: #c51313; 
}

.space-red {
    color: #c51313;
}

.space-grey {
    color: #cfcfd1;
}

.app_container {
    width: 100%;
    height: 100vh;
    background-image:url(/static/media/junk_one.a66c5331.gif), url(/static/media/space_background.25959697.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat, repeat;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-auto-rows: repeat(4, 25%);
    grid-template-areas: 
    ". . . ."
    "page-container page-container page-container navbar-comp"
    "page-container page-container page-container navbar-comp"
    "page-container page-container page-container navbar-comp";
}

.page-container {
    grid-area:page-container;
    width: 100%;
    height: 100%;
    margin: 20px; 
}

.navbar-comp {
    grid-area: navbar-comp;
    align-self: center;
    justify-self: end;
}

.earth_home_button {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-image: url(/static/media/earth.04e425e1.png);
    background-position: center;
    background-size: contain;
}

h1 {
    font-size: 40px;
    font-family: 'Courgette', cursive;
}

.bigSpace {
    background-image: url(/static/media/joel-filipe-191372-unsplash.6b170b4f.jpg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100vh;
    border-radius: 5px;
    opacity: 0.8;
}

.navColor {
    background-color:#4F3345;
}

nav {
    font-family: 'Space Mono', monospace;
}

.welcomeFont {
    font-family: 'Space Mono', monospace;
}
.spaceCard-container {
    height: 600px;
    width: 100%;
    position: relative;
    overflow-y: scroll;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  
}

.spaceCard-container::-webkit-scrollbar  { /* WebKit */
    width: 0;
    height: 0;
}

.spaceCard-heart {
    position: absolute;
    z-index: 3;
    height: 300px;
    width: 300px;
    background-image: url(/static/media/heart.d17d3d61.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    top: 40px;
    left: 35%;
}

.spaceCard-imageBox {
    width: 70%;
    margin: 0 auto;
    height: 400px;
    position: relative;
    overflow: hidden;
    -webkit-transform: rotate(12deg);
            transform: rotate(12deg);
    box-shadow: 9px 9px 9px 0px #000000ab;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat, repeat;
}

.spaceCard-image {
    position: absolute;
}

.spaceCard-horoscope-right, .spaceCard-date-right, 
.spaceCard-date-left, .spaceCard-horoscope-left, .spaceCard-center-flirt {
    font-family: 'Courgette', cursive;
    font-size: 25px;
    position: absolute;
    z-index: 2;   
}

.spaceCard-date-right {
    position: absolute;
    z-index: 2;
    top: 25px;
    right: 42px;
}

.spaceCard-horoscope-right {
    top: 134px;
    right: 0;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.spaceCard-date-left {
    position: absolute;
    z-index: 2;
    bottom: 25px;
    left: 42px;
}

.spaceCard-horoscope-left {
    position: absolute;
    z-index: 2;
    bottom: 134px;
    left: 0;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.spaceCard-center-flirt {
    margin: 112px;
}

.spaceCard-Lorem {
    position: relative;
    z-index: 4;
    margin: 30px;
    margin-top: -20px; 
    margin-bottom: 200px;
}

.spaceCard-Lorem p {
    color: var(--space-red);
    font-family: 'Space Mono', monospace;
    font-size: 5px;
}

.spaceCard-Lorem h1 {
    color: var(--space-red);
    font-size: 80px;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.spaceCard-filler {
    border-bottom: 1px solid var(--space-red);
    height: 10px;
    width: 33%;
    margin: 0 auto;
}

.spaceCardArrow {
    position: absolute;
    z-index: 3;
    top: 55px;
    height: 300px;
    width: 300px;
    background-image: url(/static/media/squiggly_arrow.030f10c7.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat, repeat;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.spaceCard-Info {
    position: relative;
    margin: 30px;
    margin-top: 20px; 
}

.spaceCard-Info p {
    font-family: 'Space Mono', monospace;
}

.spaceCard-Info h1 {
    color: var(--space-red);
    font-size: 30px;
    padding-top: 100px;
    padding-bottom: 100px;
}

.spaceCard-personalNote {
    color: black;
    font-family: 'Space Mono', monospace;
    font-size: 25px;
}

.spaceCard-nasaFact {
    color: black;
    font-family: 'Space Mono', monospace;
    font-size: 25px;
}
