@import url(https://fonts.googleapis.com/css?family=Courgette|Space+Mono);

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --space-red: #c51313; 
}

.space-red {
    color: #c51313;
}

.space-grey {
    color: #cfcfd1;
}

.app_container {
    width: 100%;
    height: 100vh;
    background-image:url("./assets/junk_one.gif"), url("./assets/space_background.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat, repeat;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-auto-rows: repeat(4, 25%);
    grid-template-areas: 
    ". . . ."
    "page-container page-container page-container navbar-comp"
    "page-container page-container page-container navbar-comp"
    "page-container page-container page-container navbar-comp";
}

.page-container {
    grid-area:page-container;
    width: 100%;
    height: 100%;
    margin: 20px; 
}

.navbar-comp {
    grid-area: navbar-comp;
    align-self: center;
    justify-self: end;
}

.earth_home_button {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-image: url("./assets/earth.png");
    background-position: center;
    background-size: contain;
}

h1 {
    font-size: 40px;
    font-family: 'Courgette', cursive;
}

.bigSpace {
    background-image: url("./assets/joel-filipe-191372-unsplash.jpg");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100vh;
    border-radius: 5px;
    opacity: 0.8;
}

.navColor {
    background-color:#4F3345;
}

nav {
    font-family: 'Space Mono', monospace;
}

.welcomeFont {
    font-family: 'Space Mono', monospace;
}